import { useNavigate, useParams, Outlet, Link } from "react-router-dom";
import { useState, useContext } from "react";
import { useEffect } from "react";
import Badge from '@mui/material/Badge';

import API from "./API";
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';

import Button from '@mui/material/Button';
import * as React from 'react';

import noteContext from './NoteContext';


export default function StudentDetails() {


    const [anchorEl, setAnchorEl] = React.useState(null);

    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };



    const [student, setStudent] = useState();

    const { sr } = useParams()

    const navigate = useNavigate();

    const a = useContext(noteContext);



    let token = a.token;



    const sendto = async (page) => {



        navigate(page);


    };


    const fetchData = async () => {

        const res = await API.get("student/" + sr, {
            headers: {
                Accept: 'application/json'
            }
        });


        setStudent(res.data);




    };






    var url = "/homework/" + sr;



    useEffect((sr) => {



        fetchData(sr);


    }, []);


    if (student == undefined) return "";


    return (

        <div>



            <div className="saksh_title">




                <strong>

                    [ {student.sr} ]    {student.name} s/o   {student.father_name}
                </strong>

                <Button
                    id="basic-button"
                    aria-controls={open ? 'basic-menu' : undefined}
                    aria-haspopup="true"
                    aria-expanded={open ? 'true' : undefined}
                    onClick={handleClick}
                >
                    Actions
                </Button>
                <Menu
                    id="basic-menu"
                    anchorEl={anchorEl}
                    open={open}
                    onClose={handleClose}
                    MenuListProps={{
                        'aria-labelledby': 'basic-button',
                    }}
                >



<MenuItem onClick={()=>sendto(url)} >   Set Homework </MenuItem>
                    <MenuItem onClick={()=>sendto( "/editstudent/"+sr)} >   Edit </MenuItem>



                </Menu>

               

               <hr />









            </div>


            <table>

                <tbody>




                    <tr>

                        <td>
                            SR No.
                        </td>
                        <td>
                            {student.sr}
                        </td>
                    </tr>



                    <tr>

                        <td>
                            Name
                        </td>
                        <td>
                            {student.name}
                        </td>
                    </tr>



                    <tr>

                        <td>
                            Father name
                        </td>
                        <td>
                            {student.father_name}
                        </td>
                    </tr>



                    <tr>

                        <td>
                            Mother Name
                        </td>
                        <td>
                            {student.mother_name}
                        </td>
                    </tr>


                    <tr>

                        <td>
                            Gender
                        </td>
                        <td>
                            {student.gender}
                        </td>
                    </tr>
                    <tr>

                        <td>
                            Admission Date
                        </td>
                        <td>
                            {student.admission_date}
                        </td>
                    </tr>

                </tbody>
            </table>



            <Outlet />


        </div>

    )
}